export default {

	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			typeList:[],
			reachGoods:[],
			page:1,
			total:0,
			total:this.$http.total().total,
			bannerUrl:this.$http.bannerUrl,
			basicUrl:this.$http.basicUrl
		};
	},

	onLoad() {
		this.loadData();
	},
	async onReachBottom() {
		this.page++;
		if(this.reachGoods.length===this.total){
			uni.showToast({
				title:"没有更多信息",
				mask:true,
				icon:"none"
			});
			return;
		}
		this.reachGoods=[...this.reachGoods,...await this.$http.goods(this.page)];
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			//轮播图
			let carouselList = await this.$http.banner();
			this.swiperLength = carouselList.length;
			this.carouselList = carouselList;
			//分类信息的获取
			// let goodsList = await this.$api.json('goodsList');
			// this.goodsList = goodsList || [];
			// this.typeList = await this.$http.type();
			//推荐商品的信息
			let reachGoods = await this.$http.goods(this.page);
			this.total=await this.$http.total();
			this.reachGoods = reachGoods;
			uni.removeStorageSync("tid");
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	},

























}
